    private b64DecodeUnicode(str) {
        return decodeURIComponent(Array.prototype.map.call(atob(str), function(c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
        }).join(''))
    }

    public load_mission_data(){
        resources.load('jms',TextAsset, (err, file) => {
            if (err) {
                console.warn(err);
                return;
            }
            let str_b64 = file.text;
            let c0:string[] = ['!','-','>','<','~'];
            let c1:string[] = ['=','0','A','N','C'];
            for (let i = 0; i < c0.length; i++) {
                str_b64 = str_b64.split(c0[i]).join(c1[i]);
            }
            //console.log(str_b64);
            let arr_b64js = str_b64.split('|');
     
            
            for (let i = 0; i < arr_b64js.length-1; i++) {
                let b64js = arr_b64js[i];
                let d64 = this.b64DecodeUnicode(b64js);
           
               
                
                let jobj = JSON.parse(d64);
                if(Math.floor(i/10) == 0){
                    this.mission_data_A.push(jobj);
                }
                else if(Math.floor(i/10)== 1){
                    this.mission_data_B.push(jobj);
                }
                else if(Math.floor(i/10) == 2){
                    this.mission_data_C.push(jobj);
                }
            }
            this.isLoadOk = true;
            console.log(
                "load_mission_data ok -> "+this.mission_data_A.length+" "+
                this.mission_data_B.length+" "+this.mission_data_C.length);
            
        });

    }

    //
    public get_mission_data():any{
        
        if(this.select_Game == 1){
            return this.mission_data_A[this.select_Mission-1];
        }
        else if(this.select_Game == 2){
            return this.mission_data_B[this.select_Mission-1];
        }
        else if(this.select_Game == 3){
            return this.mission_data_C[this.select_Mission-1];
        }
        
    }

    //
    public setWin(play_last_time:number){
        this.play_last_time_m = Math.floor((play_last_time/1000)/60);
        this.play_last_time_s = Math.floor((play_last_time/1000)%60); 
        this.play_last_time_ms = Math.floor((play_last_time%1000)/10); 


        let sec= this.play_last_time_m*60+this.play_last_time_s;
        //
        this.play_score = this.get_mission_data().Score.LevelBase;
        this.play_score += this.get_mission_data().Score.SecondPlus*sec;

        //
        this,this.myRankData[this.select_Game-1].score += this.play_score;
        this.myRankData[3].score = 0;
        for(let i=0;i<3;i++){   
            this.myRankData[3].score += this.myRankData[i].score;
        }
        //
        this.play_totle_score = this.myRankData[this.select_Game-1].score;

        if(this.select_Mission < this.GameData_Max_Mission_level[this.select_Game-1] &&
            this.select_Mission == this.play_Game_Max_Mission_level[this.select_Game-1]){
            this.select_Mission+=1;
            this.play_Game_Max_Mission_level[this.select_Game-1] = this.select_Mission;
        }
    }
    public setLose(play_last_time:number){
        this.play_last_time_m = Math.floor((play_last_time/1000)/60);
        this.play_last_time_s = Math.floor((play_last_time/1000)%60); 
        this.play_last_time_ms = Math.floor((play_last_time%1000)/10); 
        this.play_score = 0;
        this.play_totle_score = this.myRankData[this.select_Game-1].score;


    }

