import { find, EventTouch,Node,RichText, Sprite, Color,Label, instantiate,log} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST,RankData } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';
import { ns_SwitchSprite } from './ns_SwitchSprite';





//
enum R_scene_state  { 
    st_none=100,    //
    st_init,        // 
    st_end, //
}

class Proc_R_scene{ 
    
    private state:number = R_scene_state.st_none;

    private page_path_name:string[] = [
        "Canvas/BG/SpriteSplash-001/page_01","Canvas/BG/SpriteSplash-001/page_02",
        "Canvas/BG/SpriteSplash-001/page_03","Canvas/BG/SpriteSplash-001/page_04"
    ];

    //node
    private page_name:string[] = [
        "page_01","page_02","page_03","page_04"
    ];

    private my_info_type_path:string[] = [
        "Canvas/BG/playerinfo/_",
        "Canvas/BG/playerinfo/_",
        "Canvas/BG/playerinfo/_",
        "Canvas/BG/playerinfo/_",
    ];
    private my_info_type_Node:Node[] = [];

    private baseItem:Node = null;
    private cloneBase:Node = null;
    //
    private now_page:number = 1;
    
    
    public init(){
        
        this.now_page = 1;

        this.baseItem = find("Canvas/BG/SpriteSplash-001/ScrollView/view/content/basenode/item");
        this.baseItem.active = false;
        this.cloneBase = find("Canvas/BG/SpriteSplash-001/ScrollView/view/content/clone");
        for(let i=0;i<this.my_info_type_path.length;i++){
            this.my_info_type_Node[i] = find(this.my_info_type_path[i]);
        }
        
        
        
        common_bind_touch_start("Canvas/BG/Arrow",this.go_GameHome,this); 
        for(let i=0;i<this.page_path_name.length;i++){
            common_bind_touch_start(this.page_path_name[i],this.pageDown,this);
        }


        this.update_page();

        
      
    }

    private pageDown(event:EventTouch){
        let number:number = this.page_name.indexOf((event.target as Node).name);
        this.now_page = number+1;
        this.update_page();
        AudioManager.playSound("7_click01");

    }

    private update_page(){
        for(let i=0;i<this.page_path_name.length;i++){
            let _nodeNS:ns_SwitchSprite = find(this.page_path_name[i]).getComponent(ns_SwitchSprite);
            
            if(this.now_page == i+1){
                _nodeNS.switchSelect(true);
                this.my_info_type_Node[i].active = true;
                find("Canvas/BG/playerinfo/name").getComponent(Label).string = getST().myRankData[i].name;
                find("Canvas/BG/playerinfo/sc01").getComponent(Label).string = getST().myRankData[i].rank.toString();
                find("Canvas/BG/playerinfo/sc02").getComponent(Label).string = getST().myRankData[i].score.toString();

                this.cloneBase.destroyAllChildren();
                this.cloneBase.removeAllChildren();

                console.log("this.now_page = "+this.now_page);
                let rankData:RankData[] = getST().rank_data[i];
                for(let j=0;j<rankData.length;j++){
                    let _item:Node = instantiate(this.baseItem);
                    _item.active = true;
                    _item.parent = this.cloneBase;

                    _item.setPosition(0,j*(-55));
                    _item.getChildByName("num").getComponent(Label).string = rankData[j].rank.toString();
                    _item.getChildByName("name").getComponent(Label).string = rankData[j].name;
                    _item.getChildByName("score").getComponent(Label).string = rankData[j].score.toString();
                    this.cloneBase.addChild(_item);
                }
            }
            else{
                this.my_info_type_Node[i].active = false;
                _nodeNS.switchSelect(false);
            }
            

        }
    }


    private go_GameHome():void{
        AudioManager.playSound("7_click01");
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
        
    }



}



var _proc_R_scene:Proc_R_scene = null;


export function getProc_R_scene() {

    if(_proc_R_scene === null) {
        _proc_R_scene = new Proc_R_scene();
        console.log('new Proc_R_scene');
    }
    return _proc_R_scene;
} 

