import { _decorator, Component, Node,game,director,Enum,find, UITransform, Sprite ,SpriteFrame} from 'cc';
const { ccclass, property } = _decorator;





@ccclass('ns_SwitchSprite')
export class ns_SwitchSprite extends Component {


    @property({ type: SpriteFrame })
    public normal_Sprite:SpriteFrame = null;

    @property({ type: SpriteFrame })
    public selected_Sprite: SpriteFrame = null!;

    @property({ type: SpriteFrame })
    public moveOver_Sprite: SpriteFrame = null!;

    @property({ type: SpriteFrame })
    public lock_Sprite: SpriteFrame = null!;

    private _blLock:boolean = false;
    private _blSelected:boolean = false;

    public switchSelect(sw:boolean){
        this._blSelected = sw;
        let _sp = this.node.getComponent(Sprite)!;
        if(sw){
            _sp.spriteFrame = this.selected_Sprite;
        }else{
            _sp.spriteFrame = this.normal_Sprite;
        }    
    }

    public switchLock(sw:boolean){
        this._blLock = sw;
        if(this._blLock == false){
            this.switchSelect(this._blSelected);
        }
        else{
            let _sp = this.node.getComponent(Sprite)!;
            _sp.spriteFrame = this.lock_Sprite;   
        }
    }

    private onMouseEnter() {
        if(this._blLock) return;
        let _sp = this.node.getComponent(Sprite)!;
        _sp.spriteFrame = this.moveOver_Sprite;
        console.log("onMouseEnter");
        
    }
    private onMouseLeave() {
        if(this._blLock) return;
        this.switchSelect(this._blSelected);
        console.log("onMouseLeave");
    } 

    start(){
        this.switchSelect(false);
        if(this.moveOver_Sprite!=null){
             this.node.on(Node.EventType.MOUSE_ENTER, this.onMouseEnter, this);
             this.node.on(Node.EventType.MOUSE_LEAVE, this.onMouseLeave, this);
        }    
    }

    onLoad () {
        this.switchSelect(false);
    }

    
}

