import { EventTarget,Node,find,resources, TextAsset, RichText } from 'cc';
import { AudioManager } from './AudioManager';

export class CLastPlayTime {

  
    
    private richText_time:RichText = null;

    private last_play_time:number = 0;
    private blEnd:boolean = true;
    private blPlay10TimesSound:boolean = false;


    constructor() {
        this.blEnd = true;
        this.last_play_time = 0;
        this.blPlay10TimesSound = false;
    }


    public start(_last_play_time:number) {
        let node_labe_time:Node = find("Canvas/TimeText");
        this.richText_time = node_labe_time.getComponent(RichText);

        this.last_play_time =  _last_play_time;
        this.blEnd = false;
        this.blPlay10TimesSound = false;
        this.ui_update();
    }

    public getLastPlayTime():number{
        return this.last_play_time;
    }

   

    private ui_update() {
        if(this.richText_time == null) return;

        let time_s:number = Math.floor((this.last_play_time/1000)%60);
        let time_m:number = Math.floor((this.last_play_time/1000)/60); 
        let time_ms:number = Math.floor((this.last_play_time%1000)/10); 
        let time_str_s:string = time_s.toString().padStart(2, '0');
        let time_str_m:string = time_m.toString().padStart(2, '0');
        let time_str_ms:string = time_ms.toString().padStart(2, '0');
   
        this.richText_time.string = "<color=#FFFFFF>"+time_str_m+":"+time_str_s+":"+time_str_ms+"</color>";

    }

    // return true:
    //dt: 
    public update(dt: number):boolean {
        if(this.blEnd){
            return false;
        }
        this.last_play_time -= dt;
        if(this.last_play_time <= 0){
            this.last_play_time = 0;
        }

        this.ui_update();

        if(this.last_play_time <= 0){
            this.blEnd = true;
            return true;
        }
        
        if(this.blPlay10TimesSound==false){
            if(this.last_play_time <= 3000){
                this.blPlay10TimesSound = true;
                AudioManager.playSound("8_9_countdown_and_start");
                // AudioManager.playSound("10_timeout");
            }
        }
        return false;
    
    }
}



