import { assert, assetManager, log, SpriteFrame,resources } from "cc";

export class SpriteFrameManager {

    private static _cachedSpriteFrameMap: Record<string, SpriteFrame> = {};
    private static _spriteName: string[]= [];

    // init AudioManager in GameRoot component.
    public static init () {
        log('Init SpriteFrameManager !');
    }

    //
    public static isLoadOk ():boolean {
        for (let key in SpriteFrameManager._cachedSpriteFrameMap) {
            if(SpriteFrameManager._cachedSpriteFrameMap[key] == null) {
                return false;
            }
        }
        return true;
    }

    public static preloadSpriteFrame (name: string) {
        const path = `img/${name}/spriteFrame`;
        if(path in SpriteFrameManager._spriteName) {
            return;
        }
        SpriteFrameManager._spriteName.push(path);
        SpriteFrameManager._cachedSpriteFrameMap[path] = null;
        resources.load(path, SpriteFrame, (err, sprite_frame) => {
            if (err) {
                console.warn(err);
                return;
            }
            
            SpriteFrameManager._cachedSpriteFrameMap[path] = sprite_frame;
        });
    }

    public static getSpriteFrame(name: string): SpriteFrame {
        
        const path = `img/${name}/spriteFrame`;
        return SpriteFrameManager._cachedSpriteFrameMap[path];
        
    }
}