import { find, EventTouch,Node,RichText, Sprite, Color,Label} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';
import { CLastPlayTime } from './CLastPlayTime';
import { ns_SwitchSprite } from './ns_SwitchSprite';

//
enum B_scene_state  { 
    st_none=100,    //
    st_init,        // 
    st_into_animation, //
    st_start, //
    st_play_question_sound, //
    st_play_input, //
    st_show_result, //
    st_win, //
    st_timeup_animation, //
    st_lose, //
    st_end, //
}

class Proc_B_scene{ 
    
    private state:number = B_scene_state.st_none;
    private state_next:number = -1;//-1
    

    private question_number:number[] = [];//
    private anser_number:number[] = [];//
    
    
    private last_timestamp:number = 0;//FPS

    private last_wait_timestamp:number = 0;//(timestamp)

    private node_result_number:RichText[] = [];//
    private node_result_light:Sprite[] = [];//

    private node_start_last_time:Label = null;//

    private play_count:number = 0;//

    private input_btn_Number:number[] = [];//
    private input_btn_hide:boolean[] = [];//
    private input_count:number = 0;//

    private state_win_lose_show:number = 0;// 0 1 2

    //node
    private node_result_number_path:string[] = [
        "Canvas//1","Canvas//2","Canvas//3",
        "Canvas//4","Canvas//5","Canvas//6"
    ];
    
    //node
    private node_result_light_path:string[] = [
        "Canvas/tip_Light/green_light","Canvas/tip_Light/green_light-001",
        "Canvas/tip_Light/green_light-002","Canvas/tip_Light/green_light-003",
        "Canvas/tip_Light/green_light-004","Canvas/tip_Light/green_light-005",
    ];

    //node
    private node_input_number_path:string[] = [
        "Canvas/Layer 1/Node-001/1_icon","Canvas/Layer 1/Node-001/2_icon",
        "Canvas/Layer 1/Node-001/3_icon","Canvas/Layer 1/Node-001/4_icon",
        "Canvas/Layer 1/Node-001/5_icon","Canvas/Layer 1/Node-001/6_icon",
        "Canvas/Layer 1/Node-001/7_icon","Canvas/Layer 1/Node-001/8_icon",
        "Canvas/Layer 1/Node-001/9_icon"
    ];
    //node
    private node_input_number:string[] = [
        "1_icon","2_icon","3_icon","4_icon","5_icon",
        "6_icon","7_icon","8_icon","9_icon"
    ];

    private cLastPlayTime:CLastPlayTime = null;

    private blPlayCount01Sound:boolean = false;//
    
    
    public init(){
        this.set_Next_state(B_scene_state.st_init);
        this.last_timestamp = Date.now();
        
     

        common_bind_touch_start("Canvas/Arrow",this.go_select,this); 
        common_bind_touch_start("Canvas/play_icon",this.btn_play_question_sound,this); 
        common_bind_touch_start("Canvas/confirm_icon",this.btn_to_play_input,this); 

        //
        let node_lose:Node = find("Canvas/outturn_lose");
        node_lose.active = false;  
        let node_win:Node = find("Canvas/outturn_win");
        node_win.active = false;  
        let node_start:Node = find("Canvas/start");
        node_start.active = true;  
        let node_timesup:Node = find("Canvas/times_up");
        node_timesup.active = false;

        //
        this.node_start_last_time = find("Canvas/start/time").getComponent(Label);

        //
        common_bind_touch_start("Canvas/outturn_win/btn_back_mis",this.go_select,this);
        common_bind_touch_start("Canvas/outturn_lose/btn_back_mis",this.go_select,this);
        
        //
        common_bind_touch_start("Canvas/outturn_lose/btn_agn",this.go_again,this);

        this.cLastPlayTime = new CLastPlayTime();
      
    }

    private go_select():void{
        AudioManager.playSound("7_click01");
        common_getEventTarget().emit(EventEnum_Scene.Go_SelectScene);
        
    }

    private go_again():void{
        if(this.state == B_scene_state.st_lose){
            this.set_Next_state(B_scene_state.st_init);
        }
    }

    private btn_play_question_sound():void{
        if(this.state == B_scene_state.st_start){
            AudioManager.playSound("7_click01");
            this.set_Next_state(B_scene_state.st_play_question_sound);
        }
    }

    private btn_to_play_input():void{   
        if(this.state == B_scene_state.st_start){
            AudioManager.playSound("7_click01");
            this.set_Next_state(B_scene_state.st_play_input);
        }
        else if(this.state == B_scene_state.st_play_input){
            AudioManager.playSound("7_click01");
            this.set_Next_state(B_scene_state.st_start);
        }
    }


    private st_init_in():boolean{

        
        let mis_data = getST().get_mission_data();
        
        //
        this.anser_number = [];
        this.question_number = [];
        this.question_number = [];//true
        for(let i=0;i<mis_data.LevelSet.NumberCount;i++){
            let number = Math.floor(Math.random()*9)+1;
            if(mis_data.LevelSet.NumberRepeat != 'True'){
                while(this.question_number.indexOf(number)>=0){
                    number = Math.floor(Math.random()*9)+1;
                }
            }
            this.question_number.push(number);
            this.anser_number.push(-1);
            //
            console.log(number);
            
        }
        // result node
        this.node_result_number = [];
        this.node_result_light = [];
        for(let i=0;i<6;i++){
            let node_label:Node = find(this.node_result_number_path[i]);
            let richText:RichText = node_label.getComponent(RichText);
            this.node_result_number.push(richText);

            let node_label2:Node = find(this.node_result_light_path[i]);
            let sprite:Sprite = node_label2.getComponent(Sprite);
            this.node_result_light.push(sprite);
        }
        for(let i=0;i<6;i++){
            this.node_result_number[i].enabled = false;
            this.node_result_light[i].enabled=true;
            let sprite:Sprite = this.node_result_light[i].getComponent(Sprite);
            sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("");
        }
        //
        if(mis_data.LevelSet.NumberCount == 4){
            this.node_result_number.splice(5,1);
            this.node_result_number.splice(0,1);
            this.node_result_light[0].enabled=false;
            this.node_result_light[5].enabled=false;
            this.node_result_light.splice(5,1);
            this.node_result_light.splice(0,1);
        }
        else if(mis_data.LevelSet.NumberCount == 2){
            this.node_result_number.splice(4,2);
            this.node_result_number.splice(0,2);   
            this.node_result_light[0].enabled=false;
            this.node_result_light[1].enabled=false;
            this.node_result_light[4].enabled=false;
            this.node_result_light[5].enabled=false;
            this.node_result_light.splice(4,2);
            this.node_result_light.splice(0,2); 
        }

        //
        this.cLastPlayTime.start(mis_data.LevelSet.Time*1000);

        //
        let node_labe_title:Node = find("Canvas/");
        let richText_title:RichText = node_labe_title.getComponent(RichText);
        richText_title.string = "<color=#FFFFFF>"+mis_data.GameLevel+"</color>";
      
        //
        for(let i=0;i<this.node_input_number_path.length;i++){
            let node_input_number:Node = find(this.node_input_number_path[i]);
       
            node_input_number.off(Node.EventType.TOUCH_START, this.input_event, this);
            node_input_number.on(Node.EventType.TOUCH_START, this.input_event, this);
        }

        //
        let node_lose:Node = find("Canvas/outturn_lose");
        node_lose.active = false;  
        let node_win:Node = find("Canvas/outturn_win");
        node_win.active = false;  
        let node_start:Node = find("Canvas/start");
        node_start.active = true;  
        let node_timesup:Node = find("Canvas/times_up");
        node_timesup.active = false;

        //
        this.input_btn_Number = [];
        this.input_btn_hide = [];
        for (let i = 0; i < 9; i++) {
            this.input_btn_hide.push(false);
            if(mis_data.LevelSet.PositionShift == 0){
                this.input_btn_Number.push(i+1);
            }
            else if (mis_data.LevelSet.PositionShift > 0) {
                let tmp_nb = i+1-mis_data.LevelSet.PositionShift;
                if(tmp_nb<=0){
                    tmp_nb = tmp_nb+9;
                }
                this.input_btn_Number.push(tmp_nb);
            }
            else if (mis_data.LevelSet.PositionShift < 0) {
                if(Math.floor(Math.random()*2)==0){
                    this.input_btn_Number.push(i+1);
                }
                else{
                    this.input_btn_Number.unshift(i+1);
                }
            }
        }

        //
        for(let i=0;i<9;i++){
            let node_input_number:Node = find(this.node_input_number_path[i]);
            let sprite:Sprite = node_input_number.getComponent(Sprite);
            sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("L2__");
        }


        this.blPlayCount01Sound = false;

        return true;
    }

    public st_into_animation_in():boolean{
        this.last_wait_timestamp = Date.now()+3000;
        AudioManager.playSound("8_9_countdown_and_start");
        return true;
    }

    public st_into_animation_update():void{
        let last_time = this.last_wait_timestamp-Date.now();
        if(last_time>=0){
            let iLastTime = Math.floor(last_time/1000)+1;
            for(let i=0;i<3;i++){
                let node_tmp:Node = find("Canvas/start/start_"+(i+1));  
                if(iLastTime == (i+1)){
                    node_tmp.active = true;
                }
                else{
                    node_tmp.active = false;
                }
            }

            this.node_start_last_time.string = "..."+(Math.floor(last_time/1000)).toString()+"...";
        }
        else{
            let node_start:Node = find("Canvas/start");
            node_start.active = false;  
            this.set_Next_state(B_scene_state.st_play_question_sound); 
            //
            AudioManager.stopMusic();
            //AudioManager.playMusic("3_bgm03");
        }
    }

    private st_start_in():boolean{
        let mis_data = getST().get_mission_data();
        //
        for(let i=0;i<this.node_result_number.length;i++){
            if(mis_data.LevelSet.RightRetain == "True" && this.question_number[i] == this.anser_number[i]){
                continue;
            }
            this.anser_number[i] = -1;
            this.node_result_number[i].enabled = false;
            let sprite:Sprite = this.node_result_light[i].getComponent(Sprite);
            sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("");
        }
        this.input_count = 0;
        this.switchNS("Canvas/play_icon",false);
        this.switchNS("Canvas/confirm_icon",false);
        this.switchNS_lock("Canvas/play_icon",false);
        this.switchNS_lock("Canvas/confirm_icon",false);
        return true;
    }

    private st_play_question_sound_in():boolean{
        this.last_wait_timestamp = Date.now();
        this.play_count = 0;
        this.switchNS("Canvas/play_icon",true);
        this.switchNS("Canvas/confirm_icon",false);
        this.switchNS_lock("Canvas/play_icon",true);
        this.switchNS_lock("Canvas/confirm_icon",true);
        return true;
    }

    private st_play_question_sound_update():void{
        
        if(Date.now()-this.last_wait_timestamp>1000){
            this.last_wait_timestamp = Date.now();
            this.play_sound(this.question_number[this.play_count]);
            this.play_count++;
            if(this.play_count>=this.question_number.length){
                this.set_Next_state(B_scene_state.st_start);
            }
        }
    }
    private st_play_input_in():boolean{

        this.switchNS("Canvas/play_icon",false);
        this.switchNS("Canvas/confirm_icon",true);
        this.switchNS_lock("Canvas/play_icon",true);
        this.switchNS_lock("Canvas/confirm_icon",false);
        return true;
    }


    private st_play_input_update():void{
        let blComplete:boolean = true;
        for(let i=0;i<this.anser_number.length;i++){
            if(this.anser_number[i] == -1){
                blComplete = false;
                break;
            }
        }
        if(blComplete){
            this.set_Next_state(B_scene_state.st_show_result);
        }
    }

    private st_show_result_in():boolean{
        this.last_wait_timestamp = Date.now();

        //
        for(let i=0;i<this.anser_number.length;i++){
            let anumber = this.anser_number[i];
            let sprite:Sprite = this.node_result_light[i].getComponent(Sprite);

            if(anumber == this.question_number[i]){
                sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("");
            }
            else{
                //
                let mis_data = getST().get_mission_data();
                let blHint = false;
                for(let j=0;j<mis_data.LevelSet.HintPosition.length;j++){
                    if(mis_data.LevelSet.HintPosition[j] == (i+1)){
                        blHint = true;
                        break;
                    }
                }
                if(blHint == false){//
                    sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("");
                }
                else{//
                    let blFind = false;
                    for(let i=0;i<this.question_number.length;i++){
                        if(this.question_number[i] == anumber){
                            blFind = true;
                            break;
                        }
                    }
                    if(blFind){
                        sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("");
                    }
                    else{
                        sprite.spriteFrame = SpriteFrameManager.getSpriteFrame("");
                    }
                }
            }
        }
        //
        let bl_right = true;
        for(let i=0;i<this.question_number.length;i++){
            if(this.question_number[i] != this.anser_number[i]){
                bl_right = false;
                break;
            }
        }
        if(bl_right){
            AudioManager.playSound("12_answer01");
        }
        else{
            AudioManager.playSound("13_answer02");
        }

        return true;
    }
    
    private st_show_result_update():void{
        if(Date.now()-this.last_wait_timestamp>=1000){
            //
            let bl_right = true;
            for(let i=0;i<this.question_number.length;i++){
                if(this.question_number[i] != this.anser_number[i]){
                    bl_right = false;
                    break;
                }
            }
            if(bl_right){
                this.set_Next_state(B_scene_state.st_win);
            }
            else{
                this.set_Next_state(B_scene_state.st_start);
            }
        }
    }
    private st_win_in():boolean{

        getST().setWin(this.cLastPlayTime.getLastPlayTime());
        let node_win:Node = find("Canvas/outturn_win");
        node_win.active = true;
        
        this.state_win_lose_show = 0;
        this.last_wait_timestamp = Date.now();
        
        //
        let sc_01:Label = find("Canvas/outturn_win/SpriteSplash/Node/sc_01").getComponent(Label);
        sc_01.enabled = false;
        
        sc_01.string = 
        getST().play_last_time_m.toString().padStart(2, '0')+":"+
        getST().play_last_time_s.toString().padStart(2, '0')+":"+
        getST().play_last_time_ms.toString().padStart(2, '0');

        //
        let sc_02:Label = find("Canvas/outturn_win/SpriteSplash/Node/sc_02").getComponent(Label);
        sc_02.string = "0";
        sc_02.enabled = false;

        //
        let sc_03:Label = find("Canvas/outturn_win/SpriteSplash/Node/sc_03").getComponent(Label);
        sc_03.string = "0";
        sc_03.enabled = false;

        //
        let node_btn_back:Node = find("Canvas/outturn_win/btn_back_mis");
        node_btn_back.active = false;

        AudioManager.stopMusic();
        AudioManager.playSound("4_bgm04");
        
        return true;
    }
    private st_win_update():void{
        if(this.state_win_lose_show == 0){
            if(Date.now()-this.last_wait_timestamp>=1000){
                let sc_01:Label = find("Canvas/outturn_win/SpriteSplash/Node/sc_01").getComponent(Label);
                sc_01.enabled = true;
                this.state_win_lose_show = 1;
                this.last_wait_timestamp = Date.now();
              
            }
        }
        else if(this.state_win_lose_show == 1){
            let showscore = Math.floor((getST().play_score/Math.pow(2, 20))*Math.pow(2, (Date.now()-this.last_wait_timestamp)/50));
            if(Date.now()-this.last_wait_timestamp>=1000){
                showscore = getST().play_score;
                this.state_win_lose_show = 2;
                this.last_wait_timestamp = Date.now();
            }

            let sc_02:Label = find("Canvas/outturn_win/SpriteSplash/Node/sc_02").getComponent(Label);
            if(sc_02.enabled == false)
                sc_02.enabled = true;
            sc_02.string = showscore.toString();

            
        }
        else if(this.state_win_lose_show == 2){
            let showscore = Math.floor((getST().play_totle_score/Math.pow(2, 20))*Math.pow(2, (Date.now()-this.last_wait_timestamp)/50));
            if(Date.now()-this.last_wait_timestamp>=1000){
                showscore = getST().play_totle_score;
                this.state_win_lose_show = 3;
                this.last_wait_timestamp = Date.now();
            }
            //
            let sc_03:Label = find("Canvas/outturn_win/SpriteSplash/Node/sc_03").getComponent(Label);
            if(sc_03.enabled == false)
                sc_03.enabled = true;
            sc_03.string = showscore.toString();

            if(this.blPlayCount01Sound == false){
                if(Date.now()-this.last_wait_timestamp>=200){
                    this.blPlayCount01Sound = true;
                    AudioManager.playMusic("6_count01");
                }
            }

        }
        else if(this.state_win_lose_show == 3){
            let node_btn_back:Node = find("Canvas/outturn_win/btn_back_mis");
            node_btn_back.active = true;
            this.state_win_lose_show = 4;
            AudioManager.stopMusic();
        } 
    }

    public st_timeup_animation_in():boolean{
        this.last_wait_timestamp = Date.now();
        let node_timeup:Node = find("Canvas/times_up");
        node_timeup.active = true;
        return true;
    }

    public st_timeup_animation_update():void{
        if(Date.now()-this.last_wait_timestamp>=2000){
            this.set_Next_state(B_scene_state.st_lose);
            let node_timeup:Node = find("Canvas/times_up");
            node_timeup.active = false;
        }
    }

    private st_lose_in():boolean{
        this.last_wait_timestamp = Date.now();
        let node_lose:Node = find("Canvas/outturn_lose");
        node_lose.active = true;

        this.state_win_lose_show = 0;
        this.last_wait_timestamp = Date.now();

        let sc_01:Label = find("Canvas/outturn_lose/SpriteSplash/sc_01").getComponent(Label);
        sc_01.enabled = false;

        let sc_02:Label = find("Canvas/outturn_lose/SpriteSplash/sc_02").getComponent(Label);
        sc_02.enabled = false;

        //
        let sc_03:Label = find("Canvas/outturn_lose/SpriteSplash/sc_03").getComponent(Label);
        sc_03.enabled = false;
        sc_03.string = getST().play_totle_score.toString();

        let node_btn_back = find("Canvas/outturn_lose/btn_back_mis");
        node_btn_back.active = false;

        let node_btn_agn = find("Canvas/outturn_lose/btn_agn");
        node_btn_agn.active = false;

        AudioManager.stopMusic();

        return true;
    }
    private st_lose_update():void{
        if(this.state_win_lose_show == 0){
            if(Date.now()-this.last_wait_timestamp>=1000){
                let sc_01:Label = find("Canvas/outturn_lose/SpriteSplash/sc_01").getComponent(Label);
                sc_01.enabled = true;
                this.state_win_lose_show = 1;
                this.last_wait_timestamp = Date.now();
               // AudioManager.playSound("5_info");//
            }
        }
        else if(this.state_win_lose_show == 1){
            if(Date.now()-this.last_wait_timestamp>=1000){
                let sc_02:Label = find("Canvas/outturn_lose/SpriteSplash/sc_02").getComponent(Label);
                sc_02.enabled = true;
                this.state_win_lose_show = 2;
                this.last_wait_timestamp = Date.now();
            }
        }
        else if(this.state_win_lose_show == 2){
            if(Date.now()-this.last_wait_timestamp>=1000){
                let sc_03:Label = find("Canvas/outturn_lose/SpriteSplash/sc_03").getComponent(Label);
                sc_03.enabled = true;
                this.state_win_lose_show = 3;
                this.last_wait_timestamp = Date.now();
            }
        }
        else if(this.state_win_lose_show == 3){
            if(Date.now()-this.last_wait_timestamp>=1000){
                this.state_win_lose_show = 4;
        
                let node_btn_back = find("Canvas/outturn_lose/btn_back_mis");
                node_btn_back.active = true;

                let node_btn_agn = find("Canvas/outturn_lose/btn_agn");
                node_btn_agn.active = true;
            }
        }  
    }

    public input_event(event:EventTouch){
 
        let number:number = this.node_input_number.indexOf((event.target as Node).name)+1;
        
        //
        let real_number:number = this.input_btn_Number[number-1];

        if(this.state == B_scene_state.st_show_result){//
            this.last_wait_timestamp = Date.now()-20000;
            return;
        }

        //
        if(!(this.state == B_scene_state.st_play_input || this.state == B_scene_state.st_show_result)){
            if(this.state == B_scene_state.st_start){
                this.play_sound(real_number);
                if(this.state == B_scene_state.st_start){
                    //
                    if(this.input_btn_hide[number-1] == false){
                        let mis_data = getST().get_mission_data();
                        if(mis_data.LevelSet.ShowNumber == "True"){
                            //
                            this.input_btn_hide[number-1] = true;
                            let sprite:Sprite = (event.target as Node).getComponent(Sprite);
                            let strFrameName:string[] = [ "1","2","3","4","5","6","7","8","9"];
                            sprite.spriteFrame = SpriteFrameManager.getSpriteFrame(strFrameName[real_number-1]);
                        }
                    }
                }
            }
            return;
        }

        let mis_data = getST().get_mission_data();

        //
        if(mis_data.LevelSet.RightRetain == "True"){
            for(let i=this.input_count;i<this.anser_number.length;i++){
                if(this.anser_number[i] == -1){
                    break;   
                }
                this.input_count++;
            }
        }

        if(this.input_count>=mis_data.LevelSet.NumberCount){
            return;
        }

        //
        if(this.input_btn_hide[number-1] == false){
            if(mis_data.LevelSet.ShowNumber == "True"){
                //
                this.input_btn_hide[number-1] = true;
                let sprite:Sprite = (event.target as Node).getComponent(Sprite);
                let strFrameName:string[] = [ "1","2","3","4","5","6","7","8","9"];
                sprite.spriteFrame = SpriteFrameManager.getSpriteFrame(strFrameName[real_number-1]);
            }
        }

        this.anser_number[this.input_count] = real_number;

        //
        this.node_result_number[this.input_count].string = "<color=#FFFFFF>"+real_number+"</color>";
        this.node_result_number[this.input_count].enabled = true;

        this.input_count++;

        this.play_sound(real_number);
        
    }


    public set_Next_state(state:number):boolean{
        console.log("set_Next_state:"+state);
        if(this.state_next == -1){
            this.state_next = state;
            return true;
        }
        return false;
    }

    
    private _state_change(){
        if(this.state_next == -1){
            return;
        }

        let real_change:boolean = true;
        switch(this.state_next){
            case B_scene_state.st_init:
                real_change = this.st_init_in();
                break;
            case B_scene_state.st_into_animation:  
                real_change = this.st_into_animation_in();
                break;
            case B_scene_state.st_start:
                real_change = this.st_start_in();
                break;
            case B_scene_state.st_play_question_sound:
                real_change = this.st_play_question_sound_in();
                break;
            case B_scene_state.st_play_input:
                real_change = this.st_play_input_in();
                break;
            case B_scene_state.st_show_result:
                real_change = this.st_show_result_in();
                break;
            case B_scene_state.st_win:
                real_change = this.st_win_in();
                break;
            case B_scene_state.st_timeup_animation:
                real_change = this.st_timeup_animation_in();
                break;
            case B_scene_state.st_lose: 
                real_change = this.st_lose_in();
                break;
            

        }
        if(real_change){
            this.state = this.state_next;
            this.state_next = -1;
        }
    }

    private _state_update(){

        switch(this.state){
 
            case B_scene_state.st_init:
                this.set_Next_state(B_scene_state.st_into_animation);
                break;
            case B_scene_state.st_into_animation:  
                this.st_into_animation_update();   
                break;
            case B_scene_state.st_play_question_sound:
                this.st_play_question_sound_update();
                break;
            case B_scene_state.st_play_input:
                this.st_play_input_update();
                break;
            case B_scene_state.st_show_result:
                this.st_show_result_update();
                break;
            case B_scene_state.st_win:
                this.st_win_update();
                break;
            case B_scene_state.st_timeup_animation:
                this.st_timeup_animation_update();
                break;
            case B_scene_state.st_lose:
                this.st_lose_update();
                break;

        }
    }
    


    //
    private play_sound(_number:number){
        let mis_data = getST().get_mission_data();
        
        //
        let str_sound_Flute:string[] = [
            "23_flute_do","24_flute_re","25_flute_mi","26_flute_fa","27_flute_so",
            "28_flute_la","29_flute_si","30_flute_do1","31_flute_re1"];
        let str_sound_Guitar:string[] = [
            "32_guitar_do","33_guitar_re","34_guitar_mi","35_guitar_fa","36_guitar_so",
            "37_guitar_la","38_guitar_si","39_guitar_do1","40_guitar_re1"];
        let str_sound_Piano:string[] = [
            "14_Piano_Do","15_Piano_Re","16_Piano_Mi","17_Piano_Fa","18_Piano_So",
            "19_Piano_La","20_Piano_Si","21_Piano_Do_1","22_Piano_Re_1"];
               

        if(mis_data.LevelSet.timbre==1){
            AudioManager.playSound(str_sound_Piano[_number-1]);
        }
        else if(mis_data.LevelSet.timbre==2){
            AudioManager.playSound(str_sound_Flute[_number-1]);
        }
        else{
            AudioManager.playSound(str_sound_Guitar[_number-1]);
        }
        
    }


    private switchNS(nodeName:string,sw:boolean){
        let node:Node = find(nodeName);
        node.getComponent(ns_SwitchSprite).switchSelect(sw);
    }
    private switchNS_lock(nodeName:string,sw:boolean){
        let node:Node = find(nodeName);
        node.getComponent(ns_SwitchSprite).switchLock(sw);
    }

    public update(){
        //
        let elapsed_time = Date.now() - this.last_timestamp;
        
        if(elapsed_time < 33){
            return;
        }
        this.last_timestamp = Date.now();
  


        //
        if(this.state == B_scene_state.st_play_input || 
            this.state == B_scene_state.st_play_question_sound ||
            this.state == B_scene_state.st_start){
                //------------------------
                if(getST().getIsInTestMode()){
                    elapsed_time = 0;   
                }
                //---------------------------------
                if(this.cLastPlayTime.update(elapsed_time)){
                    this.set_Next_state(B_scene_state.st_timeup_animation);
                }
        }

        this._state_update();
        this._state_change();  
    }
    
}



var _proc_B_scene:Proc_B_scene = null;


