import { find, UITransform,Node,RichText,Label} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST } from './Common';
import { AudioManager } from './AudioManager';

class Proc_GameHome{ 
    public game_scene_name:string[]=["A_scene","B_scene","C_scene","Rankings"];
    private game_scene_lable:string[]=["","","",""];

    //
    private btn_goLogin_name:string[] = ["Canvas/back/Button"];

    //
    private btn_goGame_name:string = "Canvas/PageView";

    //Info
    private btn_goInfo_name:string = "Canvas/info/Button";

    //_
    private btn_Left_name: string = "Canvas/_L";
    //_
    private btn_Right_name: string = "Canvas/_R";


    public gh_page:number=1;//
   
    
    public init():void{
        this.updatePage();

        //
        for(let i=0;i<this.btn_goLogin_name.length;i++){
            common_bind_touch_start(this.btn_goLogin_name[i],this.gh_show_exit,this);
        }
        common_bind_touch_start("Canvas/Exit_game/_",this.goLogin_Y,this);
        common_bind_touch_start("Canvas/Exit_game/_",this.goLogin_N,this);


        common_bind_touch_start(this.btn_goGame_name,this.goGame,this);
        common_bind_touch_start("Canvas/RichText",this.goGame,this);
        common_bind_touch_start(this.btn_Left_name,this.gh_page_left,this);
        common_bind_touch_start(this.btn_Right_name,this.gh_page_right,this);
        common_bind_touch_start(this.btn_goInfo_name,this.goInfo,this);

        if(getST().get_bl_open_test_debug()){
            let debug_btn:Node = find("Canvas/debug_btn");
            debug_btn.active = true;

            debug_btn.on(Node.EventType.TOUCH_START, function(){
                if(getST().switchTestMode()){
                    find("Canvas/debug_btn/Label_sw").getComponent(Label).string = "Open";
                }else{
                    find("Canvas/debug_btn/Label_sw").getComponent(Label).string = "Close";
                }      
            }, this);

            if(getST().getIsInTestMode()){
                find("Canvas/debug_btn/Label_sw").getComponent(Label).string = "Open";
            }
            else{
                find("Canvas/debug_btn/Label_sw").getComponent(Label).string = "Close";
            }
        }
    }

    public gh_show_exit(){
        AudioManager.playSound("7_click01");
        let node:Node = find("Canvas/Exit_game");
        node.active = true;
    }

    private goLogin_Y(){
        AudioManager.stopMusic();
        AudioManager.playSound("7_click01");
        window.location.href = "../index.html";
    }

    public goLogin_N(){
        AudioManager.playSound("7_click01");
        let node:Node = find("Canvas/Exit_game");
        node.active = false;
    }

    private goInfo(){
        common_getEventTarget().emit(EventEnum_Scene.Go_Info);
        AudioManager.playSound("7_click01");
    }

    private goGame():void{
        if(this.gh_page == 4){
            common_getEventTarget().emit(EventEnum_Scene.Go_Rankings);
        }else{
            getST().select_Game = this.gh_page;//
            common_getEventTarget().emit(EventEnum_Scene.Go_SelectScene);
        }
        AudioManager.playSound("7_click01");
    }

    public gh_page_left(){
      
        AudioManager.playSound("7_click01");
        if(this.gh_page>1){
            this.gh_page--;
        }
        else{
            this.gh_page = this.game_scene_name.length;  
        }
        this.updatePage();
    }

    

  
    
    public gh_page_right(){
 
        AudioManager.playSound("7_click01");
        let gh_MaxPage = this.game_scene_name.length;
        
        if(this.gh_page<gh_MaxPage){
            this.gh_page++;
        }
        else{
            this.gh_page = 1;  
        }
        this.updatePage();
    }

    public getGameSceneName():string{
        return this.game_scene_name[this.gh_page-1];
    }


    public updatePage(): void{
    
        
        let node:Node = find("Canvas/PageView/view/content");
        let contentNode:UITransform = node.getComponent(UITransform);
    
        if(contentNode){
            contentNode.setAnchorPoint((this.gh_page-1)*0.25,0.5);
        }

        let node_label:Node = find("Canvas/RichText");
        let richText:RichText = node_label.getComponent(RichText);
     
        
        if(richText){
            richText.string = "<b>"+"<color=#FFFFFF>"+this.game_scene_lable[this.gh_page-1]+"</color>"+"</b>";
        }
    }
    
}



var _proc_gameHome:Proc_GameHome = null;


export function getProc_GameHome() {

    if(_proc_gameHome === null) {
        _proc_gameHome = new Proc_GameHome();
        console.log('new Proc_GameHome');
    }
    return _proc_gameHome;
} 

