import { find, UITransform,Node,RichText,math,EventTouch} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST } from './Common';
import { AudioManager } from './AudioManager';

class Proc_Select_scene{ 

    public select_Game:number=1;//
    public select_Mission:number=1;//

    private btn_mis_path:string[] = [
        "Canvas/BG/ScrollView/view/content/mis_01","Canvas/BG/ScrollView/view/content/mis_02",
        "Canvas/BG/ScrollView/view/content/mis_03","Canvas/BG/ScrollView/view/content/mis_04",
        "Canvas/BG/ScrollView/view/content/mis_05","Canvas/BG/ScrollView/view/content/mis_06",
        "Canvas/BG/ScrollView/view/content/mis_07","Canvas/BG/ScrollView/view/content/mis_08",
        "Canvas/BG/ScrollView/view/content/mis_09","Canvas/BG/ScrollView/view/content/mis_10",
        "Canvas/BG/ScrollView/view/content/mis_11","Canvas/BG/ScrollView/view/content/mis_12",
        "Canvas/BG/ScrollView/view/content/mis_13","Canvas/BG/ScrollView/view/content/mis_14",
        "Canvas/BG/ScrollView/view/content/mis_15","Canvas/BG/ScrollView/view/content/mis_16",
        "Canvas/BG/ScrollView/view/content/mis_17","Canvas/BG/ScrollView/view/content/mis_18",
        "Canvas/BG/ScrollView/view/content/mis_19","Canvas/BG/ScrollView/view/content/mis_20"
    ]

    //
    private node_input_mis:string[] = [
        "mis_01","mis_02","mis_03","mis_04","mis_05",
        "mis_06","mis_07","mis_08","mis_09","mis_10",
        "mis_11","mis_12","mis_13","mis_14","mis_15",
        "mis_16","mis_17","mis_18","mis_19","mis_20"
    ]
    //
    private node_mission_info:RichText[] = [];
    
    public init():void{
        this.select_Game = getST().select_Game;
        this.select_Mission = getST().play_Game_Max_Mission_level[this.select_Game-1];
        getST().select_Mission = this.select_Mission;
        
        common_bind_touch_start("Canvas/BG/Arrow",this.go_game_home,this);
        common_bind_touch_start("Canvas/BG/SpriteSplash-001/go_game",this.go_game,this);

        for(let i=0;i<this.btn_mis_path.length;i++){
            let node_mark_btn:Node = find(this.btn_mis_path[i]+"/mark");
            let node_mis_btn:Node = find(this.btn_mis_path[i]);
            node_mis_btn.off(Node.EventType.TOUCH_START, this.input_mission_event, this);
            if(i<=(this.select_Mission-1)){
                node_mark_btn.active = false;
                node_mis_btn.on(Node.EventType.TOUCH_START, this.input_mission_event, this);
            }
            else{
                node_mark_btn.active = true;
            }
        }
        let node_mission_info_path:string[] = [
            "Canvas/BG/SpriteSplash-001/RichText_1",
            "Canvas/BG/SpriteSplash-001/RichText_2",
            "Canvas/BG/SpriteSplash-001/ScrollView/view/content/RichText_3",
        ]
        for(let i=0;i<node_mission_info_path.length;i++){
            let node_label_tmp:Node = find(node_mission_info_path[i]);
            this.node_mission_info[i] = node_label_tmp.getComponent(RichText);
        }

        this.update_SelectF_and_info();
    }

    public input_mission_event(event:EventTouch){
        let mission:number = this.node_input_mis.indexOf((event.target as Node).name)+1;
        this.select_Mission = mission;
        getST().select_Mission = mission;
        this.update_SelectF_and_info();
        AudioManager.playSound("7_click01");
    }


    public update_SelectF_and_info():void{
        let node_btn:Node = find(this.btn_mis_path[this.select_Mission-1]);
        let node_sf:Node = find("Canvas/BG/ScrollView/view/content/selectF");
        let btn_pos:math.Vec3 = node_btn.getPosition();
        node_sf.setPosition(btn_pos.x,btn_pos.y+56,0);

        let mis_data:any = getST().get_mission_data();
        
        
        this.node_mission_info[0].string = "<color=#FFFFFF>"+mis_data.Information.Difficulty+"</color>";
        this.node_mission_info[1].string = "<color=#FFFFFF>"+mis_data.LevelSet.Time+"</color>";
        this.node_mission_info[2].string = "<color=#FFFFFF>"+mis_data.Information.Description+"</color>";//mis_data.Information.Description
        console.log(mis_data.Information.Description);
        
    }

    private go_game_home():void{
        AudioManager.playSound("7_click01");
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
    }

    private go_game():void{
        AudioManager.playSound("7_click01");
        if(this.select_Game==1){
            common_getEventTarget().emit(EventEnum_Scene.Go_Game_A);
        }
        else if(this.select_Game==2){
            common_getEventTarget().emit(EventEnum_Scene.Go_Game_B);
        }
        else if(this.select_Game==3){
            common_getEventTarget().emit(EventEnum_Scene.Go_Game_C);
        }
    }
}



var _proc_Select_scene:Proc_Select_scene = null;


export function getProc_Select_scene() {

    if(_proc_Select_scene === null) {
        _proc_Select_scene = new Proc_Select_scene();
        console.log('new Proc_Select_scene');
    }
    return _proc_Select_scene;
} 

