import { assert, assetManager, AudioClip, AudioSource, log } from "cc";

export class AudioManager {
    private static _audioSource?: AudioSource;
    private static _cachedAudioClipMap: Record<string, AudioClip> = {};
    private static _soundName: string[]= [];
  
  

    // init AudioManager in GameRoot component.
    public static init (audioSource: AudioSource) {
        log('Init AudioManager !');
        AudioManager._audioSource = audioSource;
    }

    //
    public static isLoadOk ():boolean {
        for (let key in AudioManager._cachedAudioClipMap) {
            if(AudioManager._cachedAudioClipMap[key] == null) {
                return false;
            }
        }
        return true;
    }

    public static playMusic (name: string) {
        const path = `mp3/${name}`;
        let cachedAudioClip = AudioManager._cachedAudioClipMap[path];
        if (cachedAudioClip) {
            const audioSource = AudioManager._audioSource!;
            audioSource.stop();
            audioSource.clip = cachedAudioClip;
            audioSource.play();

            console.log(`AudioManager play music ${name}`);
        }
    }

    public static stopMusic () {
        const audioSource = AudioManager._audioSource!;
        audioSource.stop();
    }

    

    public static preloadSound (name: string) {
        const path = `mp3/${name}`;
        if(path in AudioManager._soundName) {
            return;
        }
        AudioManager._soundName.push(path);
        AudioManager._cachedAudioClipMap[path] = null;
        assetManager.resources?.load(path, AudioClip, (err, clip) => {
            if (err) {
                console.warn(err);
                return;
            }
            
            AudioManager._cachedAudioClipMap[path] = clip;
        });
    }

    public static playSound(name: string) {
        
        const audioSource = AudioManager._audioSource!;
        assert(audioSource, 'AudioManager not inited!');
        
        console.log(`AudioManager play sound ${name}`);
        
        const path = `mp3/${name}`;
        let cachedAudioClip = AudioManager._cachedAudioClipMap[path];
        if (cachedAudioClip) {
            audioSource.playOneShot(cachedAudioClip, 1);
        } else {
            if(path in AudioManager._soundName) {
                return;
            }
            AudioManager._soundName.push(path);
            AudioManager._cachedAudioClipMap[path] = null;
            assetManager.resources?.load(path, AudioClip, (err, clip) => {
                if (err) {
                    console.warn(err);
                    return;
                }
                
                AudioManager._cachedAudioClipMap[path] = clip;
                audioSource.playOneShot(clip, 1);
            });
        }
    }
}