import { find, UITransform,Node,RichText,AudioClip,resources} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start,getST } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';

class Proc_Login{ 

    //
    private btn_goGamehome_name:string[] = [
        "Canvas/1/icon/Google_icon-001","Canvas/1/icon/Facebook_icon","Canvas/1/icon/apple",
        "Canvas/1/button/Button-002","Canvas/1/button/Button-003","Canvas/stroy/toHome"
    ];

    public isRunning:boolean = false;

    private isLoading:boolean = false;
    private wait_loading_time:number = 0;
    

    public init():void{
        

        //
        for(let i=0;i<this.btn_goGamehome_name.length;i++){
            common_bind_touch_start(this.btn_goGamehome_name[i],this.input_event,this);
        }

        let _node:Node = find("Canvas/stroy");
        let _node2:Node = find("Canvas/3");
        _node.active = false;
        _node2.active = true;

        this.isLoading = true;
        this.wait_loading_time = Date.now()+1000;
        this.isRunning = true;

        

    }

    public update():void{
        if(this.isLoading && (Date.now() > this.wait_loading_time)){
            if(getST().isLoadOk == false){
                return;
            }
            if(AudioManager.isLoadOk() == false){
                return;
            }
            if(SpriteFrameManager.isLoadOk() == false){
                return;
            }
            this.isLoading = false;
            let _node:Node = find("Canvas/stroy");
            let _node2:Node = find("Canvas/3");
            _node.active = true;
            _node2.active = false;
            AudioManager.playMusic("2_bgm02");
        }

    }

    private input_event(){
        this.isRunning = false;
        common_getEventTarget().emit(EventEnum_Scene.Go_GameHome);
        AudioManager.playSound("7_click01");
    }
    
}



var _proc_login:Proc_Login = null;


export function getProc_Login() {

    if(_proc_login === null) {
        _proc_login = new Proc_Login();
        console.log('new Proc_Login');
    }
    return _proc_login;
} 

