import { _decorator, Component, Node,game,director,Enum,find, UITransform, RichText,AudioSource } from 'cc';
const { ccclass, property } = _decorator;
import { common_getEventTarget,EventEnum_Scene,getST } from './Common';
import { AudioManager } from './AudioManager';
import { SpriteFrameManager } from './SpriteFrameManager';

import { getProc_Login } from './Proc_Login';
import { getProc_GameHome } from './Proc_GameHome';
import { getProc_Select_scene } from './Proc_Select_scene';
import { getProc_A_scene } from './Proc_A_scene';
import { getProc_B_scene } from './Proc_B_scene';
import { getProc_C_scene } from './Proc_C_scene';
import { getProc_R_scene } from './Proc_R_scene';
import { getProc_Info } from './Proc_Info';



@ccclass('main')
export class main extends Component {


    @property({ type: String })
    public playname:String = "";

    @property(AudioSource) 
    _audioSource: AudioSource = null!;


    onLoad () {
        console.log("main onLoad"); 
        getST().init();//

        let node = this.node;
        game.addPersistRootNode(node);

        
        common_getEventTarget().on(EventEnum_Scene.Go_GameHome, this.go_GameHome);
        common_getEventTarget().on(EventEnum_Scene.Go_Login, this.go_Login);

        common_getEventTarget().on(EventEnum_Scene.Go_SelectScene, this.go_SelectScene);
        common_getEventTarget().on(EventEnum_Scene.Go_Game_A, this.go_Game_A);
        common_getEventTarget().on(EventEnum_Scene.Go_Game_B, this.go_Game_B);
        common_getEventTarget().on(EventEnum_Scene.Go_Game_C, this.go_Game_C);
        common_getEventTarget().on(EventEnum_Scene.Go_Rankings, this.go_Rankings);
        common_getEventTarget().on(EventEnum_Scene.Go_Info, this.go_Info);

        const audioSource = this.node.getComponent(AudioSource)!;
        this._audioSource = audioSource;
        
        AudioManager.init(this._audioSource);

        //
        let str_sound_Flute:string[] = [
            "23_flute_do","24_flute_re","25_flute_mi","26_flute_fa","27_flute_so",
            "28_flute_la","29_flute_si","30_flute_do1","31_flute_re1"];
        let str_sound_Guitar:string[] = [
            "32_guitar_do","33_guitar_re","34_guitar_mi","35_guitar_fa","36_guitar_so",
            "37_guitar_la","38_guitar_si","39_guitar_do1","40_guitar_re1"];
        let str_sound_Piano:string[] = [
            "14_Piano_Do","15_Piano_Re","16_Piano_Mi","17_Piano_Fa","18_Piano_So",
            "19_Piano_La","20_Piano_Si","21_Piano_Do_1","22_Piano_Re_1"];

        let str_sound_SoundEffect:string[] = [
            "8_9_countdown_and_start","7_click01","11_click02","12_answer01","13_answer02",
            "5_info","6_count01","10_timeout"
        ];

        let str_music:string[] = ["1_bgm01","2_bgm02","3_bgm03","4_bgm04"];
               
        for(let i:number = 0;i<str_sound_Flute.length;i++){
            AudioManager.preloadSound(str_sound_Flute[i]);
        }
        for(let i:number = 0;i<str_sound_Guitar.length;i++){
            AudioManager.preloadSound(str_sound_Guitar[i]);
        }
        for(let i:number = 0;i<str_sound_Piano.length;i++){
            AudioManager.preloadSound(str_sound_Piano[i]);
        }
        for(let i:number = 0;i<str_sound_SoundEffect.length;i++){
            AudioManager.preloadSound(str_sound_SoundEffect[i]);
        }
        for(let i:number = 0;i<str_music.length;i++){
            AudioManager.preloadSound(str_music[i]);
        }


        //
        let str_spriteFrame:string[] = [
            "1","2","3","4","5","6","7","8","9",
            "l_1","l_2","l_3","l_4","l_5","l_6","l_7","l_8","l_9",
            "L1_icon_1","L1_icon_2","L1_icon_3","L1_icon_4","L1_icon_5",
            "L1_icon_6","L1_icon_7","L1_icon_8","L1_icon_9",
            "yellow_light","red_light","green_light",
            "L1__","L2__","L3__","L3_3_",
            "","","","","",
        ];
        for(let i:number = 0;i<str_spriteFrame.length;i++){
            SpriteFrameManager.preloadSpriteFrame(str_spriteFrame[i]);
        }

    }


    start() {
        console.log("main start");    
        console.log(this.playname);
        getProc_Login().init(); 

    }

    update(deltaTime: number) {
        if(getProc_Login().isRunning){
            getProc_Login().update();
        }
        switch(getST().Update_Scean){
            case 1:
                getProc_A_scene().update();
                break;
            case 2:
                getProc_B_scene().update();
                break;
            case 3:
                getProc_C_scene().update();
                break;
        }
    }
 
    go_GameHome(){
        console.log("go_GameHome"); 
        director.loadScene("GameHome",function(){
            getProc_GameHome().init();
            getST().Update_Scean = 0;
            AudioManager.playMusic("1_bgm01");
        });
    }

    go_Login(){
        console.log("go_Login");
        director.loadScene("Login",function(){
            getProc_Login().init();
            getST().Update_Scean = 0;
        });
    }

    go_SelectScene(){
        console.log("go_SelectScene");
        director.loadScene("SelectMission",function(){
            getProc_Select_scene().init();
            getST().Update_Scean = 0;
            AudioManager.playMusic("2_bgm02");
        });
    }

    go_Game_A(){
        director.loadScene(getProc_GameHome().getGameSceneName(),function(){
            getProc_A_scene().init();
            getST().Update_Scean = 1;
        });
    }

    go_Game_B(){
        director.loadScene(getProc_GameHome().getGameSceneName(),function(){
            getProc_B_scene().init();
            getST().Update_Scean = 2;
        });
    }

    go_Game_C(){
        director.loadScene(getProc_GameHome().getGameSceneName(),function(){
            getProc_C_scene().init();
            getST().Update_Scean = 3;
        });
    }
    go_Rankings(){
        director.loadScene(getProc_GameHome().getGameSceneName(),function(){
            getProc_R_scene().init();
            getST().Update_Scean = 4;
        });

    }

    go_Info(){
        console.log("go_Info");
        director.loadScene("Info",function(){
            getProc_Info().init();
        });
    }

    
}

